<div class="app" id="app"><div class="youtube-app-container"><youtube-controller class="controller"></youtube-controller><v-switch class="switch-style" v-model="isPlaylistOn" :label="isPlaylistOn ? 'Playlist On' : 'Playlist Off'" :class="{activated: isPlaylistOn}"></v-switch><h3>Current Video: {{ currentVideo.name }}</h3><youtube-video class="video" :videoId="videoId"></youtube-video><side-bar-menu class="side-bar-menu" :isPlaylistOn="isPlaylistOn"></side-bar-menu></div></div>